import React from 'react';

const TableHeader = () => {
    return (
        <thead>
            <tr>
                <th scope="col">#</th>
                <th scope="col">Name</th>
                <th scope="col">Job</th>
                <th scope="col">Phone</th>
                <th scope="col">Remove</th>
            </tr>
        </thead>
    )
}

const TableBody = (props) => {
    const rows = props.naukersData.map((row, index) => {
        return (
            <tr key={index}>
                <td>{index+1}</td>
                <td>{row.name}</td>
                <td>{row.job}</td>
                <td>{row.phone}</td>
                <td>
                    <button className="btn btn-danger" onClick={() => props.removeNauker(index)}>Delete</button>
                </td>
            </tr>
        )
    })
    return (
        <tbody>{rows}</tbody>
    )
}

const Table = props => {
    const { naukersData, removeNauker } = props

    return (
        <table className="table table-bordered">
            <TableHeader />
            <TableBody naukersData={naukersData} removeNauker={removeNauker} />
        </table>
    )
}

export default Table